import React from "react";
import styles from './sender.module.css'
import {Field, reduxForm} from "redux-form";
import {maxLengthCretaor, requiredField} from "../../../tools/validator";
import {TextArea} from "../FormsControls/FormsControls";

const maxLength = maxLengthCretaor(30)

const Sender = (props) => {
    return (
        <form className={styles.inputArea} onSubmit={props.handleSubmit}>
            <Field component={TextArea} name={"input__area"} rows="3"
                   placeholder="Введите ваше сообщение здесь..." className={styles.ta}
                   validate = {[requiredField, maxLength]}/>
            <button type="submit" className={styles.myButton}>Отправить</button>
        </form>
    )
}

const ReduxSender = reduxForm({form:'senderForm'})(Sender)

export default ReduxSender;