import React from "react";
import ProfileComponent from "./ProfileComponent";
import {connect} from "react-redux";
import {setUserProfile, updStatus} from "../../redux/posts-reducer";
import {withRouter} from 'react-router-dom'
import {compose} from "redux";

class ProfileContainer extends React.Component {
    render() {
        return <ProfileComponent {...this.props}
                                 profile = {this.props.profile}
                                 status = {this.props.status}
                                 updStatus = {this.props.updStatus}/>
    }

    componentDidMount() {
        const userId =  this.props.match.params.userId || this.props.my
        if (userId) {
            this.props.setUserProfile(userId)
        } else {
            this.props.history.push('/login')
        }
    }
}

let mapStateToProps = (state) => ({
    profile: state.profile.profile,
    status: state.profile.status,
    isAuth: state.auth.isAuth,
    my: state.auth.userId
})

export default compose(connect(mapStateToProps, {setUserProfile, updStatus}), withRouter)(ProfileContainer)