import React from "react";
import style from './style.module.css'

class ProfileStatus extends React.Component {

    state = {
        status: this.props.status || '',
        editMode: false,
        text: this.props.status
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevProps.status !== this.props.status){
            this.setState({staus: this.props.status, text: this.props.text})
        }
    }

    activateEditMode = () => {
        this.setState({editMode: true})
    }
    deactivateEditMode = () => {
        this.setState({editMode: false, status: this.state.text})
        this.props.updStatus(this.state.text)
    }

    onInput = text => {
        this.setState({text})
    }

    render() {
        return (
            <>
                {!this.state.editMode ?
                    <div className={style.status} onDoubleClick={this.activateEditMode}>
                        <h1>{this.state.status || 'Без статуса'}</h1>
                    </div> :
                    <div className={style.status}>
                        <input autoFocus={true} onBlur={this.deactivateEditMode} value={this.state.text}
                        onInput={(e) => this.onInput(e.target.value)}/>
                    </div>
                }
            </>
        )
    }

}

export default ProfileStatus