import React from "react";
import cls from './ProfileComponent.module.css'
import MyPostsContainer from "./MyPosts/MyPostsContainer";
import Burger from "../AssetComponents/Loaders/burger-loader";
import ProfileStatus from "../AssetComponents/Status/ProfileStatus";
import ProfileStatusWithHooks from "../AssetComponents/Status/ProfileStatusWithHooks";

const ProfileComponent = (props) => {
    if (!props.profile) {
        return <Burger/>
    } else {

        const photo = props.profile.photos.large
            || 'https://cdn8.arora.pro/e/upload/44f3affd-256e-42ab-a1e5-8b6199b2c71f/original//9ca325d0-ba6c-4aeb-af96-aa6900a87341.jpg'

        return (
            <div className={cls.content}>
                <ProfileStatusWithHooks status = {props.status} updStatus = {props.updStatus}/>
                <div className={cls.mainImage}>
                    <img src={photo}/>
                </div>
                <div className={cls.aboutMe}>
                    <div>Имя: {props.profile?.fullName}</div>
                </div>
                <MyPostsContainer/>
            </div>
        )
    }
}

export default ProfileComponent