import MyPosts from "./MyPosts";
import Post from "./Post/Post";
import {connect} from "react-redux";
import {addPostActionCreator} from "../../../redux/posts-reducer";

const mapStateToProps = state => {
    const posts = state.profile.posts && state.profile.posts.map(el =>
        <Post message = {el.text} date = {el.date} key = {el.id}/>)
    return {
        posts,
        // actionType: state.addPostActionCreator,
        // writeType: state.writePostActionCreator,
        // currentText: state.profile.newPostText,
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        onSend: (value) => {dispatch(addPostActionCreator(value))},
    }
}

const MyPostsContainer = connect(mapStateToProps, mapDispatchToProps)(MyPosts)

export default MyPostsContainer